body{font-family:Source Sans Pro;margin:0;background:#231f20}body .view{position:relative;display:flex;align-items:center;justify-content:center;overflow:hidden;height:100vh;border-radius:10px;transition:opacity .3s ease}body .view__loader{position:relative;box-sizing:border-box;border:16px solid #f7f7f7;border-top-color:#231f20;border-radius:50%;width:15%;-webkit-animation:spin .6s linear infinite;animation:spin .6s linear infinite;transition:.3s;opacity:1}body .view__loader:after{content:"";display:block;padding-bottom:100%}body .view__cover{transition:.3s;opacity:0}body .view .nav__back{position:absolute;transition:-webkit-filter,-webkit-transform .25s ease;transition:filter,transform .25s ease;transition:filter,transform .25s ease,-webkit-filter,-webkit-transform .25s ease;-webkit-filter:brightness(79%) sepia(60%) hue-rotate(0deg) saturate(280%);filter:brightness(79%) sepia(60%) hue-rotate(0deg) saturate(280%);width:4.9%;top:7%;left:19.6%}body .view .nav a:focus img,body .view .nav a:hover img{cursor:pointer;-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-filter:none;filter:none}body .view .cover .scroll{position:absolute;top:18%;left:21%;width:58%;height:79%}body .view .cover__gallery{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-gap:2.5em 1em;margin-bottom:8%;padding:1em}body .view .cover__gallery .gallery__item{position:relative;width:10em;height:13em;overflow:hidden;border-radius:5em 5em 0 0;transition:box-shadow .3s;margin:auto}body .view .cover__gallery .gallery__item:focus,body .view .cover__gallery .gallery__item:hover{cursor:pointer;box-shadow:.4em 0 1em 0 rgba(49,13,42,.82)}body .view .cover__gallery .gallery__item .image img{width:10em;height:13em;-o-object-fit:cover;object-fit:cover}body .view .cover__gallery .gallery__item .info{position:absolute;bottom:-45%;left:0;width:100%;height:45%;background:rgba(49,13,42,.94);transition:bottom .3s}body .view .cover__gallery .gallery__item .info .title{color:#fff;font-size:.945em;padding:1.1em .9em}body .view .cover__gallery .gallery__item:focus .info,body .view .cover__gallery .gallery__item:hover .info{bottom:0}